.group-main {
    position: absolute;
    bottom: 110px;
    right: 160px;
    width: 566px;
    height: 400px;
    background: url('./images/bg.png') no-repeat;
    cursor: pointer;
    .title {
       margin-top: 16px;
       font-size: 36px;
       color: #fff;
       text-align: center;
       line-height: 46px;
    }
    img {
        transform: translate(-30px, -90px);
    }
    .label {
        position: absolute;
        top: 50%;
        left: 50%;
        display: flex;
        width: 300px;
        height: 58px;
        line-height: 58px;
        background: linear-gradient(to right, #001eff , rgba(0, 30,255, 0));
        font-size: 30px;
        color: #fff;
        
        b {
            display: inline-block;
            background: #fff800;
            margin-right: 20px;
            width: 86px;
            height: 58px;
            border-radius: 15px;
            font-size: 44px;
            color: #0051ff;
            text-align: center;
        }
    }
}


