.map-main {
    transform: translate(0, 70px);
}
/* 大地图 */
.max-map {
    position: relative;
    width: 100%;
    height: 1600px;
    background-position: 0px 163px;
    z-index: 1;
    .map-show {
      height: 1600px;
      width: 100%;
      overflow: hidden;
      .roll-box {
        position: relative;
        transition: top 1s;
      }
    }
    .map-chongqing {
      position: relative;
      margin: 0 auto;
      width: 2000px;
      height: 1600px;
      background-position: -14px -54px;
      
      svg {
        transform: translate(32px, -20px) scale(1.07, 0.95);
        animation-name: zoomIn;
        animation-duration: 1s;
        animation-fill-mode: both;
        animation-iteration-count: 1;
        image {
          pointer-events: none;
        }
      }
    }
    .map-zhucheng {
      position: relative;
      margin: 0 auto;
      width: 2000px;
      height: 1600px;
      background-position: 32px 57px;
      
      svg {
        transform: scale(1.2, .8);
        image {
          pointer-events: none;
        }
      }
    }

    .floating-window {
      box-sizing: border-box;
      display: none; 
      opacity: 0;
      position: absolute;
      top: 0;
      left: 570px;
      padding: 80px;
      width: 890px;
      height: 399px;
      background: url(./images/windon-bg.png) no-repeat;
      font-size: 32px;

      .flo-left {
        position: relative;
        float: left;
        width: 30%;
        .value {
          position: absolute;
          top: 70px;
          width: 100%;
          font-size: 50px;
          color: #ff5317;
          font-weight: bold;
          text-align:  center;
        }
        .cir-chart {
          margin: 0 auto;
          width: 190px;
          height: 190px;
          background: url(./images/windon-cir.png) no-repeat;
        }
        .name {
          margin-top: 16px;
          text-align: center;
          color: #fff;
        }
      }
      .flo-right {
        float: right;
        display: flex;
        height: 100%;
        flex-direction: column;
        width: 60%;
        p {
          flex-grow: 1;
          line-height: 50px;
        }
        .name {
          color: #a3e2ff;
        }
        .value {
          margin-left: 70px;
          color: #fff800;
        }
      }
    }

  }

.but-swop {
  position: absolute;
  left: 24%;
  top: 28%;
  cursor: pointer;
  z-index: 2;
}

.map-legend {
    position: absolute;
    bottom: 46px;
    left: 270px;
    font-size: 32px;
    color: #fff;
    p {
      margin: 64px 0;
    }

    i {
      margin-right: 36px;
      display: inline-block;
      width: 76px;
      height: 26px;
      border-radius: 8px;
    }

    p:nth-child(1) i{
      background: #72fffd;
    }
    p:nth-child(2) i{
      background: #fff800;
    }
    p:nth-child(3) i{
      background: #ff5317;
    }
}

@keyframes zoomIn {
  from {
      opacity: 0;
      transform: scale3d(.3, .3, .3);
  }

  to {
      opacity: 1;
  }
}


