.map-path-group path:hover {
  fill: rgba(22, 82, 200, 0.6);
  cursor: pointer;
}
.layer-box-bg {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  background: url(images/layer-bg.png) no-repeat;
  padding: 40px 30px 10px 20px; 
  width: 299px;
  height: 177px;
  pointer-events: none;

  .name {
    position: relative;
    padding: 5px 10px 0px 58px;
    font-size: 36px;
    color: #fff;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    &:before {
      position: absolute;
      top: -5px;
      left: -4px;
      content: url(images/layer-icon.png);
    }
    &:after {
      display: inline-block;
      position: absolute;
      right: 10px;
      top: 20px;
      content: '人';
      background: #0b18cb;
      width: 24px; 
      height: 24px;
      font-size: 14px; 
      color: #2facc6;
      line-height: 24px;
      text-align:center;
      border-radius: 6px;
    }
  }

  .value {
    padding: 5px 10px;
    color: #ffde2a;
    font-size: 24px;
  }
}


