.main-city {
    position: absolute;
    transform: translate(94px,60px);
    width: 639px;
    height: 643px;
    background: url('./images/mainBg.png') no-repeat;
    z-index: 2;

    .title {
        margin-left: -82px;
        font-size: 36px;
        color: #fff;
        text-align: center;
    }

    /* 小地图 */
    /* .little-map { */
    .map-show {
      width: 639px;
      height: 582px;
      overflow: hidden;

      .roll-box {
        position: relative;
        transition: top 1s;
      }
      .map-chongqing {
        position: relative;
        margin: 0 auto;
        width: 639px;
        height: 582px;
        background-position: -14px -54px;
        svg {
          transform: translate(32px, -20px) scale(1.07, 0.95);
          animation-name: zoomIn;
        animation-duration: 1s;
        animation-fill-mode: both;
        animation-iteration-count: 1;
          image {
            pointer-events: none;
          }
        }
      }
      .map-zhucheng {
        position: relative;
        margin: 0 auto;
        width: 639px;
        height: 582px;
        background-position: 32px 57px;
        svg {
          transform: scale(1.2, .8);
          image {
            pointer-events: none;
          }
        }
      }
    }
  /* } */
}

@keyframes zoomIn {
  from {
      opacity: 0;
      transform: scale3d(.3, .3, .3);
  }

  to {
      opacity: 1;
  }
}


