.modal-container {
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .54);
    .modal-wrapper {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10000;
        transform: translate(-50%, -50%);
        width: 2648px;
        height: 1112px;
        background: url(./images/bg.png) no-repeat center / 100% 100%;
        .btn-close {
            cursor: pointer;
            position: absolute;
            top: 16px;
            right: 32px;
            width: 80px;
            height: 80px;
            background: url(./images/icon-close.png) no-repeat center / 100% 100%;
        }
        .title {
            position: absolute;
            top: 24px;
            left: 50%;
            padding-left: 35px;
            box-sizing: border-box;
            transform: translateX(-50%);
            width: 1349px;
            height: 161px;
            background: url(./images/title.png) no-repeat center / 100% 100%;
            text-align: center;
            color: #fff;
            font-size: 66px;
            line-height: 160px;
            letter-spacing: 35px;
            text-shadow: 0 0 20px #0018ff;
        }

        .group-list {
            box-sizing: border-box;
            margin: 220px auto 0;
            width: 95%;
            padding:  0 60px;
            overflow: auto;
            max-height: 750px;
            /*Webkit自定义滚动条样式start*/
            &::-webkit-scrollbar {  /* 滚动条整体部分 */
                width:5px;
                margin-right:2px
            }
            &::-webkit-scrollbar:horizontal {
                height:0;
            }
            &::-webkit-scrollbar-track {  /* 外层轨道 */
                border-radius: 0;
            }
            &::-webkit-scrollbar-track-piece {  /*内层轨道，滚动条中间部分 */
                background-color: transparent;
                border-right: 1px solid #339dff;
                border-radius: 0;
            }
            &::-webkit-scrollbar-thumb {  /* 滑块 */
                width:7px;
                border-radius: 0;
                background: #fff;
            }
            &::-webkit-scrollbar-corner { /* 边角 */
                width: 0;
                background-color: transparent;
            }
            &::-webkit-scrollbar-thumb:hover { /* 鼠标移入滑块 */
                background: #bbb;
            }
            li {
                padding: 10px 0;
            }
            .detachment {
                display: flex;
                align-items: center;
                font-size: 36px;
                color: #a5b0ff;
                .name {
                    width: 212px;
                }
                .progress-bar {
                    position: relative;
                    margin: 0 55px;
                    flex-grow: 1;
                    background: #031f4c;
                    height: 30px;
                    border-radius: 15px;
                    cursor: pointer;
                    span {
                        position: absolute;
                        left: 30px;
                        display: inline-block;
                        width: 50%;
                        height: 100%;
                        background: -webkit-linear-gradient(left, rgba(48, 0, 255, 0), #3000ff , #00d2ff); 
                        border-radius: 15px;
                        &::after {
                            content: '';
                            display: inline-block;
                            position: absolute;
                            right: 6px;
                            top: 4px;
                            width: 22px;
                            height: 22px;
                            border-radius: 100%;
                            background: #fff;
                            box-shadow: 0 0 20px #fff;
                        }
                    }
                }
                .value {
                    font-size: 44px;
                    color: #fcff00;
                }
            }
            .details {
                position: relative;
                display: none;
                background: url(./images/li-bg.png) repeat-x center / 100% 100%;
                padding: 20px 0;
                overflow: hidden;
                &::after {
                    content: url(./images/up.png);
                    position: absolute;
                    right: 0;
                    top: 10px;
                    cursor: pointer;
                    pointer-events: auto;
                }
                & > div{
                    /* flex-grow: 1; */
                    float: left;
                    width: 50%;
                    font-size: 32px;
                    color: #a9d1ff;
                }
                .innernet,
                .internet {
                    .name {
                        padding-left: 30px;
                        margin-bottom: 30px;
                        font-size: 40px;
                        height: 75px;
                        line-height: 75px;
                        background: url(./images/title-bg.png) no-repeat;
                    }
                    .message {
                        display: flex;
                        & > span {
                            position: relative;
                            padding: 10px;
                            flex-grow: 1;
                            margin-right: 30px;
                            display: inline-block;
                            height: 68px;
                            background: rgba(3, 41, 97, 0.7);
                            border-top: 2px solid #2439a7;
                        }
                        b {
                            position: absolute;
                            bottom: 6px;
                            right: 6px;
                            color: #fff;
                            font-size: 48px;
                            sub {
                                margin-left: 10px;
                                font-size: 26px;
                                color: #a9d1ff;
                            }
                        }
                    }
                }
                .internet {
                    .message {
                        & > span {
                            background: rgba(31, 18, 131, 0.7);
                        }
                    }
                }
            }
            .active {
                .detachment {
                    .progress-bar {
                        span {
                            background: -webkit-linear-gradient(left, rgba(48, 0, 255, 0), #3000ff, #a7c256, #fcff00); 
                        }
                    }
                }
            }
        }
    }
}




